import React from 'react';
import { Layout, BodyContainer } from './layout.styles';
import { useDeviceDetect } from '../hooks/use-device-detect/use-device-detect.hook';
import { Header as MobileHeader } from '../ui/mobile-header/header.component';
import { Menu } from '../ui/mobile-menu/menu.component';
import { ThemeProvider } from 'styled-components';
import { theme } from '../theme/theme.config';
import { useMobileMenuState } from '../hooks/use-mobile-menu/use-mobile-menu.hook';
import { Footer } from '../ui/footer/footer';
import { DesktopNavigation } from '../ui/desktop-navigation/desktop-navigation.component';
import {
  useScrollDirection,
  ScrollDirection,
} from '../hooks/use-scroll-direction/use-scroll-direction.hook';

export const LayoutProvider = ({ children }: { children: React.ReactNode }) => {
  const { isMobile } = useDeviceDetect();
  const {
    state: { isVisible },
  } = useMobileMenuState();

  const [isSticky, setSticky] = React.useState(false);

  const stickyRef = React.useRef(null);
  const scrollDirection = useScrollDirection({});

  const dirRef = React.useRef(scrollDirection);

  const handleScroll = () => {
    console.log(scrollDirection);

    if (
      stickyRef.current &&
      window.pageYOffset > stickyRef.current.getBoundingClientRect().top &&
      dirRef.current === ScrollDirection.UP
    ) {
      setSticky(true);
    } else {
      setSticky(false);
    }
  };

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', () => handleScroll);
    };
  }, []);

  return (
    <Layout>
      <style jsx global>
        {`
          html,
          body {
            margin: 0;
            padding: 0;
            font-family: 'Montserrat', 'Open Sans', sans-serif;
            background-color: #f9f9f9;
            -webkit-overflow-scrolling: touch;
          }
        `}
      </style>
      <ThemeProvider theme={theme}>
        <React.Fragment>
          <BodyContainer>
            {isMobile ? (
              <React.Fragment>
                <MobileHeader />
                {isVisible && <Menu />}
                <div>{children}</div>
              </React.Fragment>
            ) : (
              <div>
                <DesktopNavigation isSticky={isSticky} />
                <div ref={stickyRef}>{children}</div>
              </div>
            )}
          </BodyContainer>
          <Footer />
        </React.Fragment>
      </ThemeProvider>
    </Layout>
  );
};
